const defaulStimuli = [
  [1, 0],
  [2, 5],
  [3, 10],
  [4, 15]
]
export default class StimuliService {
  getStimuli (i = 0) {
    const stimuli = {
      correct: defaulStimuli[i],
      vals: []
    }

    stimuli.vals.push({ val: defaulStimuli[i] })
    stimuli.vals.push({ val: [2, 0] })
    stimuli.vals.push({ val: [2, 5] })
    stimuli.vals.sort(() => Math.random() - 0.5)
    return stimuli
  }
}
