<template>
  <div class="clock" :class="[edit ? 'clock-edit' : 'clock-view']">
    <div
      v-for="i of 12"
      :key="i"
      class="clock_time"
      :class="[edit ? 'clock_time-edit' : 'clock_time-view']"
      :style="{ transform: 'rotate(' + (i - 1) * 30 + 'deg)' }"
    >
      <div
        class="clock_time_number"
        :style="{ transform: 'rotate(' + (i - 1) * -30 + 'deg)' }"
      >
        {{ ((i + 10) % 12) + 1 }}
      </div>
    </div>
    <div
      class="clock_arm clock_arm-hour"
      :style="{ transform: 'rotate(' + (hour * 30 + minute / 2) + 'deg)' }"
    ></div>
    <div
      class="clock_arm clock_arm-minute"
      :style="{ transform: 'rotate(' + minute * 6 + 'deg)' }"
    ></div>
  </div>
</template>

<script>
export default {
  name: 'Clock',
  props: {
    time: {
      type: Array,
      default: () => []
    },
    edit: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    value: {
      get () {
        return this.time
      },
      set (value) {
        this.$emit('update:time', value)
      }
    },
    hour () {
      return this.time[0] ? this.time[0] : 0
    },
    minute () {
      return this.time[1] ? this.time[1] : 0
    }
  },
  data: () => ({})
}
</script>

<style lang="scss">
.clock {
  display: flex;
  flex: 1;
  border-radius: 50%;
  position: relative;
  border: 1px solid #000;
  overflow: hidden;
  width: 100%;
  height: 100%;
  &-view {
    cursor: pointer;
  }
  &::before {
    content: "";
    display: block;
    position: absolute;
    border-radius: 50%;
    width: 8%;
    height: 8%;
    top: 46%;
    left: 46%;
    background-color: #000;
  }
  &_time {
    position: absolute;
    width: 100%;
    height: 100%;
    text-align: center;
    transform-origin: center;
  }
  &_arm {
    display: block;
    position: absolute;
    left: 45%;
    top: 10%;
    width: 10%;
    transform-origin: center 100%;
    height: 40%;
    &::before {
      content: "";
      display: block;
      position: absolute;
      background-color: #000;
      bottom: 0;
    }
    &-hour {
      &::before {
        height: 70%;
        left: 30%;
        right: 30%;
        border-radius: 10px;
      }
    }
    &-minute {
      &::before {
        height: 100%;
        left: 35%;
        right: 35%;
        border-radius: 8px;
      }
    }
  }
}
</style>
