<template>
  <div class="home">
    <v-container>
      <v-row class="text-center">
        <v-col cols="12">
          <v-btn v-if="stimuli.correct" size="x-large" @click="play"
            >{{ stimuli.correct[0] }}:{{ stimuli.correct[1]
            }}<v-icon> mdi-volume-high </v-icon></v-btn
          >
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="4" v-for="(stimulus, i) in stimuli.vals" :key="i">
          <v-btn class="home_clock">
            <app-clock
              v-model:time="stimulus.val"
              @click="answer(stimulus.val)"

            />
          </v-btn>
        </v-col>
      </v-row>
      <v-row class="text-center">
        <v-col cols="12">
          <h1>{{ correct ? "正解！" : "" }}</h1>
        </v-col>
      </v-row>
    </v-container>
    <audio class="home_mp3" ref="mp3">
      <source
        v-if="stimuli.correct"
        :src="
          require(`@/assets/audio/${stimuli.correct[0]}-${stimuli.correct[1]}.mp3`)
        "
        type="audio/mpeg"
      />
      Your browser does not support the audio element.
    </audio>
    <audio class="home_mp3" ref="correct">
      <source :src="require(`@/assets/audio/correct.mp3`)" type="audio/mpeg" />
      Your browser does not support the audio element.
    </audio>
    <audio class="home_mp3" ref="wrong">
      <source :src="require(`@/assets/audio/wrong.mp3`)" type="audio/mpeg" />
      Your browser does not support the audio element.
    </audio>
  </div>
</template>

<script>
import StimuliService from '@/services/stimuli.service'

export default {
  name: 'Home',
  data: () => ({
    stimuliService: new StimuliService(),
    stimuli: {},
    correct: null
  }),
  mounted () {
    this.getStimuli()
  },
  methods: {
    getStimuli () {
      this.stimuli = this.stimuliService.getStimuli()
    },
    play () {
      this.$refs.mp3.play()
    },
    answer (answer) {
      this.correct =
        JSON.stringify(answer) === JSON.stringify(this.stimuli.correct)
      if (this.correct) {
        this.$refs.correct.play()
      } else {
        this.$refs.wrong.play()
      }
    }
  }
}
</script>

<style lang="scss">
.home {
  &_clock.v-btn.v-btn--density-default {
    margin: 20px auto;
    width: 27vw;
    height: 27vw;
    max-width: 202px;
    max-height: 202px;
    padding: 0;
    border-radius: 50%;
  }
  &_mp3 {
    display: none;
  }
}
</style>
